@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Oswald:wght@200..700&family=Raleway:ital,wght@0,100..900;1,100..900&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  border: none;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Oswald", sans-serif;
}

p {
  font-family: "Raleway", sans-serif;
}

.comment-content {
  font-family: "Raleway", sans-serif;
  margin-left: 5px;
}

table.text-secondary td,
table.text-secondary th {
  color: inherit;
}

.avatar-mini {
  height: 1.5rem;
  width: 1.5rem;
  object-fit: cover;
}

.avatar-sm {
  height: 2rem;
  width: 2rem;
  object-fit: cover;
}

.avatar-md {
  height: 3.5rem;
  width: 3.5rem;
  object-fit: cover;
}

.avatar-lg {
  height: 9rem;
  width: 9rem;
  object-fit: cover;
}

.icon-mini {
  font-size: 1.5rem;
}

.icon-sm {
  font-size: 2rem;
}

.icon-md {
  font-size: 3.5rem;
}

.icon-lg {
  font-size: 9rem;
}

.fa-heart,
.fa-comment {
  font-size: 1.2rem;
}

/* トップ( / )の見え方 */
.post-like-btn.is-home i.fa-heart {
  top: 1px;
  line-height: 1;
  position: relative;
}

/* 個別show( /post/{id}/show )だけ少し持ち上げたい例 */
.post-like-btn.is-show i.fa-heart {
  top: -5px;
  line-height: 1;
  position: relative;
}

/* ハート＋カウントを縦中央に */
.js-like-btn {
  display: inline-flex; /* ← これが効く */
  align-items: center; /* 縦中央 */
  gap: 0.25rem; /* &nbsp;の代わり */
}

/* ベースライン差を殺す */
.js-like-btn i.fa-heart,
.js-like-btn [id^=like-count-] {
  line-height: 1;
  display: inline-block;
  vertical-align: middle;
}

.text-skate {
  background-color: #0d6efd;
  color: white;
}

.xsmall {
  font-size: 0.7rem;
}

.badge.badge-sm {
  font-size: 0.7rem; /* ← 小さめ */
  padding: 0.25em 0.4em; /* ← デフォルトより少し狭く */
}

.img-sm {
  height: 2rem;
  width: 3rem;
  object-fit: cover;
}

.img-lg {
  height: 9rem;
  width: 13rem;
  object-fit: cover;
}

.img-md {
  height: 5rem;
  width: 5rem;
  object-fit: cover;
}

.image-lg {
  position: relative;
  width: auto;
  height: 9rem;
  object-fit: cover;
}

/* 画像に対する削除ボタンの位置調整 */
.delete-photo-btn {
  position: absolute;
  bottom: 5px;
  left: -5px;
  padding: 5px;
  z-index: 10; /* ボタンが他の要素の背後に隠れないように */
}

.image-xl {
  width: auto;
  height: 15rem;
  object-fit: cover;
}

.grid-img {
  width: 100%;
  height: 300px;
  object-fit: cover;
}

.nishimoo {
  width: 200px;
}

.description {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
  text-overflow: ellipsis;
}

.body-container .description {
  overflow: hidden;
}

.mobile-center {
  margin-left: 1rem;
}

.post-photo-container {
  height: 650px; /* 表示したい高さを固定 */
  overflow: hidden; /* はみ出した部分は隠す */
}

.post-photo {
  width: 100%; /* 横幅を基準に合わせる */
  height: 100%; /* コンテナの高さにフィット */
  object-fit: cover; /* はみ出す部分はトリミング */
}

.js-slick {
  visibility: hidden;
}

.js-slick.slick-initialized {
  visibility: visible;
}

#scrollTopBtn {
  display: none; /* 初期は非表示 */
  position: fixed;
  bottom: 40px;
  right: 40px;
  z-index: 99;
  border: none;
  outline: none;
  background-color: #0d6efd; /* Bootstrap primary */
  color: white;
  cursor: pointer;
  padding: 12px 16px;
  border-radius: 50%;
  font-size: 20px;
  transition: opacity 0.3s;
}

#scrollTopBtn:hover {
  background-color: #0b5ed7;
}

@media screen and (max-width: 1200px) {
  .post-photo {
    height: 500px;
    object-fit: cover;
  }
}
@media screen and (max-width: 768px) {
  .nishimoo {
    width: 150px;
  }
  .mobile-center {
    display: flex !important;
    flex-direction: row !important;
    justify-content: center;
    width: 100%;
  }
  .delete-photo-btn {
    position: absolute;
    bottom: 5px;
    left: -5px;
    padding: 5px;
    z-index: 10; /* ボタンが他の要素の背後に隠れないように */
  }
  .post-body {
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15);
  }
  .post-photo-container {
    height: 295px; /* スマホ用に小さく */
    overflow: hidden;
  }
  .post-photo {
    width: 100%; /* 横幅優先 */
    height: 100%;
    object-fit: cover;
  }
  .nav-link {
    margin: 0 10px 0 10px;
  }
  .description {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
.top-badge {
  font-size: 10px; /* デフォルトの小さい画面用 */
}

@media (min-width: 768px) {
  .top-badge {
    font-size: 16px; /* md以上の画面で大きく */
  }
}
@media (min-width: 1200px) {
  .top-badge {
    font-size: 18px; /* lg以上の画面でさらに大きく */
  }
}